html {
  scroll-behavior: smooth;
  scroll-padding-top: 100px;
}

/* View Transitions */
#site-header {
  view-transition-name: site-header;
}

#site-footer {
  view-transition-name: site-footer;
}

/* root 트랜지션만으로 부드럽게 전체 전환 (네비바/푸터 제외) */
::view-transition-old(root),
::view-transition-new(root) {
  animation-duration: 0.3s;
  animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

/* outfit-latin-wght-normal */
@font-face {
  font-style: normal;
  font-weight: 100 900;
  src: url(https://cdn.jsdelivr.net/fontsource/fonts/outfit:vf@latest/latin-wght-normal.woff2)
    format("woff2-variations");
  font-family: "Outfit Variable";
  font-display: swap;
  unicode-range:
    U+0000-00FF,
    U+0131,
    U+0152-0153,
    U+02BB-02BC,
    U+02C6,
    U+02DA,
    U+02DC,
    U+0304,
    U+0308,
    U+0329,
    U+2000-206F,
    U+20AC,
    U+2122,
    U+2191,
    U+2193,
    U+2212,
    U+2215,
    U+FEFF,
    U+FFFD;
}
